@import url('https://fonts.googleapis.com/css?family=Black+Han+Sans|Oswald');
@import url('https://fonts.googleapis.com/css?family=Muli:400,700');
$time: .3s;


body {
    font-family: 'Muli', sans-serif;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

.button {
    cursor: pointer;
    outline: none;
    position: relative;
    transition: $time;
    background-color: #666;
    color: #fff;
    align-self: center;
    padding: 5px 20px;
    font-size: 24px;
    border: none;
    letter-spacing: 1px;
    z-index: 0;
    overflow: hidden;
    font-weight: bold;
    margin-top: 20px;
    &:before {
        content: "";
        position: absolute;
        width: 0;
        height: 100%;
        background-color: #fff;
        left: 50%;
        top: 0;
        z-index: -1;
        transform: translate(-50%) skewX(45deg);
        transition: $time;
    }
    &:hover {
        color: black;
        &:before {
            width: 100%;
        }
    }
    &:active {
        color: black;
        &:before {
            width: 140%;
        }
    }
}

form {
    display: flex;
    flex-direction: column;
    max-width: 600px;
    margin: 50px auto;
    border: 2px solid black;
    padding: 15px;

    .error-message {
        color: red;
    }

    .label,
    .wrapper {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;
        
        &__text{
//            color: #ff4c4c;
            font-weight: bold;
            font-size: 20px;
        }
    }
    input,
    select,
    textarea {
        padding: 5px 10px;
        border: 1px solid black;
        outline: none;

        &.error {
            border: 1px solid red;
        }
    }
    .wrapper--radio {
        display: flex;
        justify-content: space-around;
        .label {
            display: flex;
            margin-bottom: 0;
            align-items: center;

            [type="radio"] {
                appearance: none;
                width: 40px;
                height: 20px;
                background-color: red;
                cursor: pointer;

                &:checked {
                    background-color: lightblue;
                }
            }
            &:first-child {
                margin-right: 20px;
            }
        }
    }
}
#popUp{
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 10px 20px;
    border: 2px solid black;
    background-color: #fff;
    display: none;
    z-index: 3;
    .text{
        text-align: center;
        font-weight: bold;
        font-size: 40px;
    }
}
#overlay{
    top: 0;
    left: 0;
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: rgba(black, 0.5);
    z-index: 2;
    display: none;
}
.modal{
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 15px;
    width: 100%;
    max-width: 400px;
    box-shadow: 0 0 2px black;
    
    .list{
        display: flex;
        flex-direction: column;
        align-items: center;
        &__item{
            list-style: none;
            font-size: 20px;
            margin-bottom: 10px;
            .name{
                color: #ff4c4c;
            }
            .value{
                color: #0c3866;
            }
        }
    }
}
    .overlay {
        position: fixed;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        background-color: rgba(black, 0.3);
    }